  .section {
    padding: 7.5rem 0;
  }
}

.container {
  margin: 0 auto;
  width: 100%;
}
@media screen and (max-width: 39.9375rem) {
  .container {
    padding: 0 1rem;
  }
}
@media screen and (min-width: 40rem) and (max-width: 47.9375rem) {
  .container {
    max-width: 40rem;
    padding: 0 2rem;
  }
}
@media screen and (min-width: 48rem) and (max-width: 63.9375rem) {
  .container {
    max-width: 48rem;
    padding: 0 3rem;
  }
}
@media screen and (min-width: 64rem) and (max-width: 79.9375rem) {
  .container {
    max-width: 64rem;
    padding: 0 4rem;
  }
}
@media screen and (min-width: 80rem) and (max-width: 95.9375rem) {
  .container {
    max-width: 80rem;
    padding: 0 5rem;
  }
}
@media screen and (min-width: 95.9375rem) {
  .container {
    max-width: 96rem;
    padding: 0 6rem;
  }
}

#countdown-widget {
  width: 100vw;
  padding: 2.5rem auto;
}

#footer {
  padding: 1.25rem 0;
  border-top: 1px solid #5D5C65;
  background-color: #34333E;
}

.footer__wrapper {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}
@media screen and (min-width: 64rem) {
  .footer__wrapper {
    flex-direction: row;
    justify-content: space-between;
  }
}

.footer__brand {
  margin-bottom: 1.25rem;
}
@media screen and (min-width: 64rem) {
  .footer__brand {
    margin-bottom: unset;
  }
}

.footer__menu {
  display: flex;
  flex-direction: row;
  justify-content: center;
  align-items: center;
  flex-wrap: wrap;
}
@media screen and (min-width: 64rem) {
  .footer__menu {
    flex-wrap: nowrap;
  }
}

.footer__link, .footer__copyright {
  font-size: 0.875rem;
  line-height: 1.25rem;
  color: rgba(255, 255, 255, 0.5);
}

.footer__link {
  transition: color 0.3s;
}
.footer__link:not(last-of-type) {
  margin-right: 1rem;
}
.footer__link:not(last-of-type)::after {
  content: "|";
  margin-left: 0.75rem;
}
.footer__link:hover {
  color: white;
}

header {
  position: fixed;
  top: 0;
  right: 0;
  left: 0;
  z-index: 900;
  padding: 1.25rem 0;
  background-color: #34333E;
  box-shadow: 0 10px 15px -3px rgba(0, 0, 0, 0.1), 0 4px 6px -2px rgba(0, 0, 0, 0.05);
}

.header__wrapper {
  display: flex;
  flex-direction: row;
  justify-content: center;
  align-items: center;
}
@media screen and (min-width: 64rem) {
  .header__wrapper {
    justify-content: space-between;
  }
}

.header__logo {
  width: auto;
  height: 3rem;
}

.header__button {
  display: none;
}
@media screen and (min-width: 64rem) {
  .header__button {
    display: inline-flex;
    flex-direction: row;
    justify-content: center;
    align-items: center;
    position: relative;
    padding: 0.75rem 1.5rem;
    background-image: radial-gradient(#A87B4F, #86623F);
    background-color: #A87B4F;
    box-shadow: 0 10px 15px -3px rgba(0, 0, 0, 0.1), 0 4px 6px -2px rgba(0, 0, 0, 0.05);
    border-radius: 0.25rem;
    font-size: 0.875rem;
    line-height: 1;
    font-weight: 500;
    text-transform: uppercase;
    color: #FFFFFF;
    transition: opacity 0.3s;
  }
  .header__button:hover {
    opacity: 0.85;
  }
}

#hero, #course {
  background-color: #F9FAFB;
}

@media screen and (min-width: 64rem) {
  .hero__wrapper, .expect__wrapper, .course__wrapper {
    display: grid;
    grid-template-columns: repeat(2, minmax(0, 1fr));
    grid-gap: 3.75rem;
  }
}

.hero__heading, .expect__heading, .course__heading, .waitlist__heading, .closed__heading, .ready__heading {
  margin-bottom: 1.25rem;
  color: #34333E;
  text-transform: uppercase;
  letter-spacing: 0.025em;
}
@media screen and (min-width: 64rem) {
  .hero__heading, .expect__heading, .course__heading, .waitlist__heading, .closed__heading, .ready__heading {
    margin-bottom: 2.5rem;
  }
}

.hero__copy:not(last-of-type), .closed__copy:not(last-of-type), .ready__copy:not(last-of-type) {
  margin-bottom: 1.25rem;
}

@media screen and (min-width: 64rem) {
  .waitlist__wrapper, .closed__wrapper, .ready__wrapper {
    margin: 0 auto;
    max-width: 35.625rem;
  }
}

.waitlist__heading, .closed__heading, .ready__heading {
  letter-spacing: unset;
}

.waitlist__copy {
  margin-bottom: 1.25rem;
}
@media screen and (min-width: 64rem) {
  .waitlist__copy {
    margin-bottom: 2.5rem;
  }
}

.waitlist__button, .form__submit, .countdown--small__button {
  position: relative;
  display: flex;
  flex-direction: row;
  justify-content: center;
  align-items: center;
  width: 100%;
  padding: 1.5rem 3rem;
  background-image: radial-gradient(#A87B4F, #86623F);
  background-color: #A87B4F;
  box-shadow: 0 20px 25px -5px rgba(0, 0, 0, 0.1), 0 10px 10px -5px rgba(0, 0, 0, 0.04);
  border-radius: 0.5rem;
  font-size: 1rem;
  line-height: 1;
  font-weight: 500;
  text-transform: uppercase;
  color: #FFFFFF;
  transition: opacity 0.3s;
}
.waitlist__button:hover, .form__submit:hover, .countdown--small__button:hover {
  opacity: 0.85;
}

.chevron {
  margin-left: 0.5rem;
  width: 1.25rem;
  height: 1.25rem;
  background-image: url("/static/img/chevron-right.svg");
  background-repeat: no-repeat;
  background-size: contain;
  background-position: center center;
}

#testimonial, #cta {
  background-image: radial-gradient(#5D5C65, #34333E);
  background-color: #34333E;
}

.testimonial__wrapper {
  display: grid;
  grid-template-columns: repeat(1, minmax(0, 1fr));
  grid-template-rows: repeeat(5, minmax(0, 1fr));
  grid-gap: 1.875rem;
}
@media screen and (min-width: 40rem) and (max-width: 63.9375rem) {
  .testimonial__wrapper {
    grid-template-columns: repeat(2, minmax(0, 1fr));
    grid-template-rows: repeeat(3, minmax(0, 1fr));
  }
}
@media screen and (min-width: 64rem) {
  .testimonial__wrapper {
    grid-template-columns: repeat(3, minmax(0, 1fr));
    grid-template-rows: repeeat(2, minmax(0, 1fr));
  }
}

.testimonial, .cta__testimonial {
  position: relative;
  padding: 2.5rem;
  background-color: rgba(255, 255, 255, 0.15);
  border-radius: 0.5rem;
  box-shadow: 0 10px 15px -3px rgba(0, 0, 0, 0.1), 0 4px 6px -2px rgba(0, 0, 0, 0.05);
  backdrop-filter: blur(4px) saturate(110%);
  color: #D6D6D8;
  font-style: italic;
}
.testimonial::after, .cta__testimonial::after {
  content: "";
  position: absolute;
  bottom: 0;
  left: 20%;
  width: 0;
  height: 0;
  border: 2rem solid transparent;
  border-top-color: rgba(255, 255, 255, 0.15);
  border-bottom: 0;
  border-left: 0;
  margin-left: -1rem;
  margin-bottom: -2rem;
}

.avatar {
  width: 3rem;
  height: 3rem;
  border: 2px solid #FFFFFF;
  border-radius: 100vw;
}

